html {
  color-scheme: light dark;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: system-ui;
  display: flex;
}

.App {
  --df-green: #10f898;
  --df-quality-common: #d0d0d0;
  --df-quality-uncommon: #00ff3f;
  --df-quality-rare: #00bfff;
  --df-quality-epic: #f0f;
  --df-quality-legendary: #ff7f00;
  --df-quality-exotic: red;

  & .sections {
    flex-flow: wrap;
    gap: 1em;
    display: flex;

    & .section {
      & h2 {
        margin: 0 0 1em;
        padding: 0;
        font-size: 24px;
      }
    }
  }

  & .MapTimers {
    & .currentTime {
      margin-bottom: .5em;
      font-size: 18px;
      font-weight: bold;
    }

    & .timers {
      & .timer {
        border-left: 5px solid var(--df-green);
        padding: .5em;

        & h3 {
          margin: 0;
          padding: 0;
          font-size: 24px;
        }

        &.live {
          background: var(--df-green);
          color: #000;
          font-weight: bold;
        }

        & .time {
          font-size: 18px;
        }
      }
    }
  }

  & .Collector {
    & .mission {
      & h3 {
        margin-bottom: .5em;
        font-size: 18px;
        font-weight: bold;
      }

      & .items {
        & .item {
          --item-color: transparent;
          border-left: 4px solid #0000;
          border-color: var(--item-color);
          width: 100%;
          box-shadow: inset 100px 0 100px -100px var(--item-color);
          flex-flow: row;
          justify-content: space-between;
          gap: 1em;
          margin-bottom: 4px;
          padding: 2px 0 2px .5em;
          display: flex;

          &.common {
            --item-color: var(--df-quality-common);
          }

          &.uncommon {
            --item-color: var(--df-quality-uncommon);
          }

          &.rare {
            --item-color: var(--df-quality-rare);
          }

          &.epic {
            --item-color: var(--df-quality-epic);
          }

          &.legendary {
            --item-color: var(--df-quality-legendary);
          }

          &.exotic {
            --item-color: var(--df-quality-exotic);
          }
        }
      }
    }
  }
}
